/*
* https://github.com/startbase-dev/react-form-elements?ref=reactjsexample.com
*/
import React, { useState } from 'react';
import { Input, PasswordInput, RadioGroup, Checkbox, Form, TextArea, NumberInput } from '@start-base/react-form-elements';

export default function Label({ statement }) {
    // State to hold the NumberInput value
    const [numberValue, setNumberValue] = useState('');

    // Handle changes to the NumberInput
    const handleChange = (event) => {
        // Update the state with the new value
        setNumberValue(event.target.value);
    };

    return (
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-start', position: 'relative', width: '100%' }}>
            <Form style={{ width: '100px', marginTop: '40px' }}>
                <NumberInput value={numberValue} onChange={handleChange} placeholder="0" />
            </Form>
            <div style={{ marginLeft: '20px', textAlign: 'center', width: 'calc(100% - 120px)' }}>
                <p>{statement}</p>
            </div>
        </div>
    );
}
