import React from 'react';
import Label from '../Label'
import NavigationButtons from '../NavigationButtons';

export default function Tipo02() {

  return (
   <> 
   <h2>TIPO 02 - O CTA RISO-HUDSON</h2>
   <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', margin: '20px' }}>
      <p>Classifique as afirmaes conforme sua aplicabilidade com base na seguinte escala:</p>
      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(5, 1fr)', gap: '10px', justifyContent: 'center', maxWidth: '600px', marginTop: '20px' }}>
        <div style={{ display: 'flex', alignItems: 'center', padding: '10px', border: '1px solid black' }}><span style={{ marginRight: '10px' }}>1</span>Nunca  verdadeira</div>
        <div style={{ display: 'flex', alignItems: 'center', padding: '10px', border: '1px solid black' }}><span style={{ marginRight: '10px' }}>2</span>Raramente  verdadeira</div>
        <div style={{ display: 'flex', alignItems: 'center', padding: '10px', border: '1px solid black' }}><span style={{ marginRight: '10px' }}>3</span>Em parte  verdadeira</div>
        <div style={{ display: 'flex', alignItems: 'center', padding: '10px', border: '1px solid black' }}><span style={{ marginRight: '10px' }}>4</span>Geralmente  verdadeira</div>
        <div style={{ display: 'flex', alignItems: 'center', padding: '10px', border: '1px solid black' }}><span style={{ marginRight: '10px' }}>5</span>Sempre  verdadeira</div>
      </div>
    </div>
    <Label statement={"Meu interesse pelas pessoas leva-me a envolver-me profundamente com elas  com seus sonhos, esperanas e necessidades."} />
    <Label statement={"Para mim, ser amigvel  natural: puxo conversa facilmente e chamo todo mundo pelo nome."} />
    <Label statement={"Descobri que as pessoas reagem com afeto quando lhes dou ateno e incentivo."} />
    <Label statement={"No posso ver um cachorro sem dono que j quero levar para casa."} />
    <NavigationButtons/>
   </>
  )
}