import Tipo01 from '../forms/tipo_01'

const formsPages = [<Tipo01 />, 'Tipo 02', 'Tipo 03', 'Tipo 04', 'Tipo 05', 'Tipo 06', 'Tipo 07', 'Tipo 08', 'TIpo 09']
const getCurrentFormPage = (activeFormsPage) => formsPages[activeFormsPage]

export default function Forms({ activeFormsPage} ) {
    return(
        getCurrentFormPage(activeFormsPage)
    );
}