import React, { useState } from 'react';
import '../App.css';
import { Input, PasswordInput, RadioGroup, Checkbox, Form, TextArea } from '@start-base/react-form-elements';
import Forms from '../pages/forms'

export default function Welcome() {
    const [activeFormsPage, setActiveFormsPage] = useState(-1)
    const handleNext = () => setActiveFormsPage(activeFormsPage + 1)
    const handlePrev = () => setActiveFormsPage(activeFormsPage - 1)

  return (
    <div className="App">
    { 
    activeFormsPage < 0 ?
    <header className="App-header">
    <p>
      Seja bem vindo! Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.
    </p>
    <button onClick={handleNext}>Clique para iniciar</button>
    </header> :
    <Forms activeFormsPage={activeFormsPage} />
    }
  </div>
  )
}